.overlay {
  position: fixed;
  height: 100%;
  right: 0;
  bottom: 0;
  left: 0;
  top: 0;
  z-index: 9;
  overflow-y: scroll;
  visibility: hidden;
  opacity: 0;
  transition: all 1s;
  transition-delay: 0.5s;
  pointer-events: none;
}
.overlay::-webkit-scrollbar {
  display: none;
}
