  padding: 20px 20px;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.6);
  display: grid;
  justify-content: center;
  align-items: center;
  visibility: visible;
  opacity: 1;
  transition: all 0.6s;
  pointer-events: initial;
}
.overlay.active-mode {
  z-index: 100;
  display: grid;
  justify-content: center;
  align-items: center;
  visibility: visible;
  opacity: 1;
  transition: all 0.6s;
  pointer-events: initial;
